	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>water is an essential resource for the lives of Canadians;  </p>\n</li>\n<li>\n<p>Canada has more than 7% of the world's renewable water;  </p>\n</li>\n<li>\n<p>no federal program outlines export and marketing regulation;  </p>\n</li>\n<li>\n<p>the conservation of the' natural water' resource is enshrined in the Canada Water Resources Act, but the export and marketing of freshwater remains a gap in discussions on international agreements, pacts or treaties; </p>\n</li>\n<li>\n<p>the management of border resources must be based on agreements between the various levels of government of each member country to determine the non-market value of water and ensure that it is not exported for profit between the Boundary Waters Treaty (BWT) countries;  </p>\n</li>\n<li>\n<p>the federal government recognizes the authority of the provinces and municipalities to manage aquatic resources; </p>\n</li>\n<li>\n<p>several world organizations predict that by 2025 some 34 countries will run out of water; </p>\n</li>\n<li>\n<p>there will be strong pressure in the coming years to export water to countries experiencing drought and a lack of drinking water (United States and the Middle East). </p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> Canada recognize freshwater as a Canadian Heritage asset and grant the status as a resource to exercise full sovereignty;  </p>\n<p><strong>BE IT RESOLVED</strong> to regulate ecosystem conservation for current and future populations, steps be taken to determine federal governance with the provinces to justify nonmarket management of this resource (freshwater) and conservation of fresh water supplies.  (Quebec)</p>","frontmatter":{"path":"/policy/protecting-freshwater-resources--export-and-marketing","date":"Tue Mar 06 2018 00:08:44 GMT-0500 (EST)","title":"Protecting Freshwater Resources -Export and Marketing"}}},"pathContext":{}}

